import { Injectable } from '@angular/core';
import { HttpModule, Http, Response } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class UserServiceService {
  constructor(private http: Http) { }

  addNewUser(userDetailModel: any ) {
    return this.http.post('/rest/create', userDetailModel).map((response: Response) => {
    const createdUser = response.json();
     return createdUser;
  });
  }

  editUser(objToUpdate: any) {

   return this.http.post('/rest/update', objToUpdate).map((response: Response) => {
    const editedUser = response.json();
    return editedUser;

  });
  }

  deleteUser(objToDelete: any ) {

   return this.http.post('/rest/delete', objToDelete).map((response: Response) => {
    const Deleteduser = response.json();
     return Deleteduser;
  });
  }

  getAllUser() {

   return this.http.get('/rest/allusers').map((response: Response) => {
   const allUser = response.json();
   return allUser;

  });
  }

  getUserById(id: number ) {
    return this.http.get('/rest/getuser/' + id ).map((response: Response) => {
      const currentUser = response.json();
      return currentUser;
    });
  }

}



