import { Component, OnDestroy } from '@angular/core';
import {AuthenticationService } from '../Services/authentication.service';
import {Router} from '@angular/router';
import {AlertService} from '../Services/alert.service';
import {Role} from '../models/role';
import { GlobalMessage } from '../models/global-message';

@Component({
  selector: 'app-login',
  moduleId: module.id,
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements  OnDestroy {
  loginInfoModel: any = {};

  constructor(private auth: AuthenticationService, private router: Router, private alertservice: AlertService) {}

  ngOnDestroy()  {
  /*if (sessionStorage.getItem('currentUser')) {
      sessionStorage.removeItem('currentUser);
}*/
  }

  verifyAuthentication(): void {

    this.auth.verifyAuthentication(this.loginInfoModel).subscribe(
     userDataFromServer => {

      const objProp = 'msg'
      if ( userDataFromServer.hasOwnProperty(objProp)) {
        this.alertservice.success(userDataFromServer.msg);
      } else {

        if (userDataFromServer.role[0].role === Role.role_superAdmin){

             this.router.navigate(['/admin']);
         } else {

              this.router.navigate(['/user']);
         }
       }
     },
     error => {
            this.alertservice.error(GlobalMessage.errorMsg);
    });
 }}



