import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

@Component({
  selector: 'app-editable-row',
  templateUrl: './editable-row.component.html',
  styleUrls: ['./editable-row.component.css']
})
export class EditableRowComponent implements OnInit {

  @Input() userDetails: {id: number, name: string, userName: string, roles: object };
  @Output() onToggleUpdateAndDisplay = new EventEmitter<any>();
  @Output() onToggleCancelAndDisplay = new EventEmitter<any>();
  userRole: any = {};
  currentUserRoleId: string;

  ngOnInit(): void {
   this.currentUserRoleId = ( this.userDetails.roles[0].id).toString();
   if ( sessionStorage.getItem('currentUser')) {
    this.userRole = JSON.parse(sessionStorage.getItem('currentUser')).roles;
   }
  }
  toggleToupdateAndDisplay(): void {
    const objTosend = {
      id: this.userDetails.id,
      name: this.userDetails.name,
      roleid: this.currentUserRoleId}
    this.onToggleUpdateAndDisplay.emit(objTosend);
   }

  toggleTocancelAndDisplay(): void {
     this.onToggleCancelAndDisplay.emit();
  }

}



