import {Component, ViewChild, OnInit } from '@angular/core';
import {MatPaginator, MatTableDataSource} from '@angular/material';
import {AuditService} from '../../../Services/audit.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-audit-logs',
  templateUrl: './audit-logs.component.html',
  styleUrls: ['./audit-logs.component.css']
})
export class AuditLogsComponent implements OnInit {
  auditInfoModel: any = {};
  displayedColumns = [ 'UserName', 'Name', 'Role', 'created By', 'Created On', 'Updated By', 'Updated On'];
  dataSource: any;
  @ViewChild(MatPaginator) paginator: MatPaginator;
   constructor(private auditService: AuditService, private router: Router ) { }
  ngOnInit(): void {
    this.getAllAuditLogs();
}
  navigateToUserList() {
    this.router.navigate(['/admin']);
  }
   getAllAuditLogs(): void {
       this.auditService.getAllAudit().subscribe(masterUserInfo=> {
      this.auditInfoModel = masterUserInfo;
      this.dataSource = new MatTableDataSource<any>(this.auditInfoModel);
      this.dataSource.paginator = this.paginator;
  });
   }
}





