import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AppComponent } from './app.component';
import {routing} from './app.route';
import { LoginComponent } from './login/login.component';
import {UserListComponent} from './superAdmin/user-list/user-list.component';
import {EditableRowComponent} from './superAdmin/user-list/editable-row.component';
import {DisplayRowComponent} from './superAdmin/user-list/display-row.component';
import { HeaderComponent } from './header/header.component';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import {AuthenticationService} from './Services/authentication.service';
import {UserServiceService} from './Services/user-service.service';
import {AlertService} from './Services/alert.service';
import {LoginRouteGuardService} from './guard/login-route-guard.service';
import {NgIdleModule} from '@ng-idle/core';
import { NgIdleKeepaliveModule } from '@ng-idle/keepalive';
import { MomentModule } from 'angular2-moment';
import { AlertComponent } from './alert/alert.component';
import { customHttpProvider } from './helper/index';
import { UserComponent } from './normalUser/user/user.component';
import { AuditLogsComponent } from './superAdmin/user-list/audit-logs/audit-logs.component';
import {  MatPaginatorModule, MatProgressSpinnerModule, MatSortModule, MatTableModule } from "@angular/material";
import {AuditService} from './Services/audit.service';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ObjectToIterablePipe } from './Pipes/object-to-iterable.pipe';
import {RoleGuardService} from './guard/role-guard.service';
import { AdminComponent } from './superAdmin/admin/admin.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    UserListComponent,
    HeaderComponent,
    AlertComponent,
    EditableRowComponent,
    DisplayRowComponent,
    UserComponent,
    AuditLogsComponent,
    ObjectToIterablePipe,
    AdminComponent,
  ],

  imports: [
    BrowserModule,
    CommonModule,
    HttpClientModule,
    routing,
    FormsModule,
    HttpModule,
    NgIdleModule,
    MomentModule,
    NgIdleKeepaliveModule.forRoot(),
    MatTableModule,
    MatPaginatorModule,
    MatSortModule,
    MatProgressSpinnerModule,
    BrowserAnimationsModule
  ],
  providers: [AuthenticationService, LoginRouteGuardService, NgIdleModule, UserServiceService, AlertService, customHttpProvider, AuditService, RoleGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }



