import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import {UserListComponent} from './superAdmin/user-list/user-list.component';
import {LoginRouteGuardService} from './guard/login-route-guard.service';
import {UserComponent} from './normalUser/user/user.component';
import {Role} from './models/role';
import {RoleGuardService} from './guard/role-guard.service';
import {AuditLogsComponent} from './superAdmin/user-list/audit-logs/audit-logs.component';
import {AdminComponent} from './superAdmin/admin/admin.component';

export const routes: Routes = [{path: '', component: LoginComponent},
    {path: 'admin', component: AdminComponent, canActivate: [RoleGuardService], data : {
      expectedRole: Role.role_superAdmin }, children: [{ path: 'AuditLogs', component: AuditLogsComponent } ,
      {path: '', component: UserListComponent }] },
    {path: 'user', component: UserComponent, canActivate: [LoginRouteGuardService]},
    { path: '**', redirectTo: '' }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes, { useHash: true })



