import { Injectable, Injector } from '@angular/core';
import { ConnectionBackend, XHRBackend, RequestOptions, Request, RequestOptionsArgs, Response, Http, Headers } from "@angular/http";
import { appConfig } from '../app.config';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import { LocalStorage } from '@ng-idle/core';
import { GlobalMessage } from '../models/global-message';


@Injectable()
export class CustomHttp extends Http {
  constructor(backend: ConnectionBackend, defaultOptions: RequestOptions) {
        super(backend, defaultOptions);
    }

    private serializeObj(obj) {
        const result = [];
       for (const property in obj)
            result.push(encodeURIComponent(property) + '=' + encodeURIComponent(obj[property]));
      return result.join('&');
    }

    get(url: string, options?: RequestOptionsArgs): Observable<Response> {
      return super.get(appConfig.apiUrl + url, this.addJwt(options)).catch(this.handleError);
    }
    post(url: string, body: string, options?: RequestOptionsArgs): Observable<Response> {
      return super.post(appConfig.apiUrl + url, body, this.addJwt(options)).catch(this.handleError)
    }

    put(url: string, body: string, options?: RequestOptionsArgs): Observable<Response> {
      return super.put(appConfig.apiUrl + url, body, this.addJwt(options)).catch(this.handleError);
    }

    delete(url: string, options?: RequestOptionsArgs): Observable<Response> {
      return super.delete(appConfig.apiUrl + url, this.addJwt(options)).catch(this.handleError);
    }

    // private helper methods

    private addJwt(options?: RequestOptionsArgs): RequestOptionsArgs {
        // ensure request options and headers are not null
        options = options || new RequestOptions();
        options.headers = options.headers || new Headers();
        options.headers.append('Content-Type', 'application/json');

        // add authorization header with jwt token
        const currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
          options.headers.append('Authorization', 'Token ' + currentUser.token);
        }

        return options;
    }

    private handleError(error: any) {
      if (error.status === 401 || error.status === 408 || error.status === 403 || error.status === 0) {
        const sessionStrorgaeVal = sessionStorage.getItem('currentUser');
            if (sessionStrorgaeVal === null || sessionStrorgaeVal.length === 0 || sessionStrorgaeVal === undefined) {
            } else {
               sessionStorage.removeItem('currentUser');
            }
        window.location.href = '';
        } else if ( error.status === 500) {
         const errorBodyParse = JSON.parse(error._body).message;
            if (errorBodyParse === GlobalMessage.token_missingMsg) {
              window.location.href = '';
             } else if (errorBodyParse === GlobalMessage.deleted_userMsg) {
                window.location.href = '';
            }
      }
      return Observable.throw(error._body);
    }

    private onEnd(): void {
    }

}

export function customHttpFactory(xhrBackend: XHRBackend, requestOptions: RequestOptions): Http {
    return new CustomHttp(xhrBackend, requestOptions);
}

export let customHttpProvider = {
    provide: Http,
    useFactory: customHttpFactory,
    deps: [XHRBackend, RequestOptions]
};




