import { Injectable } from '@angular/core';
import { HttpModule, Http, Response  } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()

export class AuthenticationService {

  constructor(private http: Http) { }

  verifyAuthentication(loginInfoModel: any) {
    const loginBody = JSON.stringify(loginInfoModel);

    return this.http.post('/login', loginBody ).map((response: Response) => {

    const userDataResponse = response.json();

    if (userDataResponse && userDataResponse.token) {
         sessionStorage.setItem('currentUser', JSON.stringify(userDataResponse))
    }
     return userDataResponse;
  });
}
}



