import { Component, OnInit } from '@angular/core';
import {UserServiceService} from '../../Services/user-service.service';
import {AlertService} from '../../Services/alert.service';
import { GlobalMessage } from '../../models/global-message';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
  currentUserDetails: any = {};
  cloneCurrentUserDetails: Object = {};
  IsEdit = false;
  constructor(private userService: UserServiceService, private alertService: AlertService) { }

  ngOnInit() {

   const currentUserId = JSON.parse(sessionStorage.getItem('currentUser')).id;
   this.getCurrentUserDetailsById(currentUserId);
 }

 getCurrentUserDetailsById(currentUserId: number) {
  this.userService.getUserById(currentUserId).subscribe(masterUserInfo => {
  this.currentUserDetails = masterUserInfo ;
  this.cloneCurrentUserDetails = JSON.parse(JSON.stringify(this.currentUserDetails));
});
 }

  updateName(): void {

      const objToSend = {
       id: this.currentUserDetails.id,
      name: this.currentUserDetails.name,
      roleid: this.currentUserDetails.roles[0].id,
      userName: this.currentUserDetails.userName
    }
    this.editUser(objToSend);
    this.IsEdit = false;
  }
  cancel(): void {
    this.currentUserDetails = JSON.parse(JSON.stringify(this.cloneCurrentUserDetails));
    this.IsEdit = false;
  }

  editUser(objToUpdate: any ): void {
    this.userService.editUser(objToUpdate).subscribe(
      success => {
        if (Object.keys(success).length === 0 && success.constructor === Object) {
          this.alertService.success(GlobalMessage.errorMsg);
       } else {
          this.cloneCurrentUserDetails = JSON.parse(JSON.stringify(success));
          this.alertService.success(GlobalMessage.user_updateSuccessMsg);
       }},
      error => {
        // for persistence of data clone the object
       this.currentUserDetails = JSON.parse(JSON.stringify(this.cloneCurrentUserDetails));
      this.alertService.error('There is some problem! please try after some time later.');
      } );
  }
}





