import { Component , Input, Output, EventEmitter} from '@angular/core';
import { GlobalMessage } from '../../models/global-message';

@Component({
  selector: 'app-display-row',
  templateUrl: './display-row.component.html',
  styleUrls: ['./display-row.component.css']
})
export class DisplayRowComponent  {

  @Input() userDetails: {id: number, name: string, userName: string, roles: string };
  @Output() onToggleEdit = new EventEmitter<any>();
  @Output() onToggleDelete = new EventEmitter<any>();

  toggleToEdit(): void {
      this.onToggleEdit.emit();
  }

  toggleToDelete(id: number): void {
    if (confirm(GlobalMessage.delete_confirmationMsg)){
       this.onToggleDelete.emit(id);
    }
  }

}



