import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import {UserServiceService} from '../../Services/user-service.service';
import { masterUserInfo } from '../../models/masterUserInfo';
import {AlertService} from '../../Services/alert.service';
import { GlobalMessage } from '../../models/global-message';


@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements OnInit {
  masterUserInfoModel: any = {};
  showAddButton: Boolean = false;
  alluserDetails: any [] = [];
  displayingIndeces: boolean[];
  confirmPassword: string;
  cloneUserDetails: any = {};
  userRole: any = {};

  constructor(private userService: UserServiceService, private alertservice: AlertService) { }

  ngOnInit(): void {
    this.userRole = JSON.parse(sessionStorage.getItem('currentUser')).roles;
    this.getAllUser();
 }

  isDisplayAddButton(): void {
  this.showAddButton = true;
  }

  isDisplay(index: number ) {
      return this.displayingIndeces[index];
  }

  saveUserDetails(): void {
    if (this.masterUserInfoModel.password !== this.confirmPassword) {
      alert(GlobalMessage.password_mismatchMsg);
    } else if (this.masterUserInfoModel.userName.toUpperCase() === 'admin'.toUpperCase()) {
      alert(GlobalMessage.username_forbiddenMsg);
    } else {
     this.addNewUser(this.masterUserInfoModel);
    }
  }
  toggleEdit(index: number): void {
    this.displayingIndeces[index] = !this.displayingIndeces[index];
  }

  toggleDelete(id: any, index: number): void {
    const objToDelete = {id: id};
    this.deleteUser(objToDelete, index);
  }

  toggleUpdate(objToUpdate: any, index: number): void {
   this.editUser(objToUpdate, index);
  }
  toggleCancel(index: number): void {
   this.alluserDetails = JSON.parse(JSON.stringify(this.cloneUserDetails));
    this.displayingIndeces[index] = !this.displayingIndeces[index];
  }

  addNewUser(objToCreate: any): void {
   this.userService.addNewUser(objToCreate).subscribe(
      success => {
        const objProp = 'msg'
        if (success.hasOwnProperty(objProp)) {
          this.alertservice.success(success.msg);
        } else {

          this.alertservice.success(GlobalMessage.user_createSuccessMsg);
          // clear all model value
         this.masterUserInfoModel = {};
         this.confirmPassword = '';
         this.showAddButton = false;
         // Refresh User List
          this.getAllUser();
        }


      },
      error => {
         this.alertservice.error(GlobalMessage.errorMsg);
        }
    );
 }

  editUser(objToUpdate: any, index: number ){
    this.userService.editUser(objToUpdate).subscribe(
      success => {
        if ( Object.keys(success).length === 0 && success.constructor === Object) {
          this.alertservice.success(GlobalMessage.errorMsg);

        } else {
        this.alluserDetails[index]= success;
        this.cloneUserDetails= JSON.parse(JSON.stringify(this.alluserDetails));
         this.toggleEdit(index);
         this.alertservice.success(GlobalMessage.user_updateSuccessMsg);
        }
      },
      error => {
        // when grid is on updated mode then user click cancel button so persient data in between
        this.cloneUserDetails = JSON.parse(JSON.stringify(this.alluserDetails));
        this.alertservice.error(GlobalMessage.errorMsg);

      }

    );
  }

  deleteUser(objToDelete, index: number ) {
   this.userService.deleteUser(objToDelete).subscribe(
      success => {
        if ( Object.keys(success).length === 0 && success.constructor === Object) {
          this.alertservice.success(GlobalMessage.errorMsg);

        } else {
          this.alertservice.success(GlobalMessage.user_deleteSuccessMsg);
          this.alluserDetails.splice(index, 1 );
          this.cloneUserDetails.splice(index, 1);
        }
      },
      error => {
       this.alertservice.error(GlobalMessage.errorMsg);

      }
    );
  }
  getAllUser() {
     this.userService.getAllUser().subscribe(masterUserInfo => {
      this.alluserDetails = masterUserInfo ;
      this.cloneUserDetails = JSON.parse(JSON.stringify(this.alluserDetails));
      this.displayingIndeces = new Array(this.alluserDetails.length);
      this.displayingIndeces.fill(true);

    });
  }

}









